* {
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

html {
  height: 100%;
}

body {
  height: 100%;
  background-color: #1d1d1e;
  font-size: 16px;
}

#root {
  height: 100%;
  flex-direction: column;
  display: flex;
}

.center {
  text-align: center;
}

.bottom-space {
  margin-bottom: 2rem;
}

.main-container, .subcontent-container {
  text-align: center;
  max-width: 1200px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: 2rem 1rem;
  display: flex;
}

.grid-container {
  max-width: 1200px;
  flex-wrap: wrap;
  flex-grow: 1;
  justify-content: center;
  gap: 20px;
  margin: 0 auto;
  display: flex;
}

.grid-item {
  text-align: center;
  min-width: 250px;
  height: 150px;
  background-color: #f1f1f1;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  margin: 5px;
  padding: 20px;
  display: flex;
}

.btn {
  height: 200px;
  width: 200px;
  cursor: pointer;
  background-color: #0d85da;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  font-weight: 500;
  transition: transform .3s ease-out;
  display: flex;
}

.btn.small {
  height: 50px;
}

.btn:hover {
  background-color: #0f92ef;
  transform: scale(1.05);
}

.door-container img {
  width: 100%;
  border-radius: 1rem;
}

.anim-rocket {
  height: 200px;
  animation: 2s ease-in backwards anim-for-rocket;
}

@keyframes anim-for-rocket {
  0% {
    transform: translate(-250px, 250px);
  }

  100% {
    transform: translate(350px, -350px);
  }
}

.logo-anim-container {
  width: 300px;
  height: 300px;
  position: relative;
}

.logo-anim-container img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.logo-anim-container img:first-child {
  animation: .7s ease-out backwards leftToRight;
}

.logo-anim-container img:nth-child(2) {
  animation: .7s ease-out backwards rightToLeft;
}

.subtext {
  animation: .5s ease-out .7s backwards bottomToTop;
  display: block;
}

@keyframes leftToRight {
  0% {
    left: -500px;
  }

  100% {
    left: 0;
  }
}

@keyframes rightToLeft {
  0% {
    left: 500px;
  }

  100% {
    left: 0;
  }
}

@keyframes bottomToTop {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

